.message {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 24px;


}


.imageAndText {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    align-items: flex-end;
    margin-right: 10px;


}

.text {
    background-color: #f6f7fb;
    padding: 8px 12px;
    font-size: 16px;
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    max-width: 500px;
    min-width: 200px;
}

